body {
  padding: 20px 20px;
}

.results tr[visible='false'],
.no-result {
  display: none;
}

.results tr[visible='true'] {
  display: table-row;
}

.counter {
  padding: 8px;
  color: #ccc;
}

.container {
  width: 80%;
}

.loader {
  width: 10%;
  margin: auto;
}

@media screen and (max-width: 600px) {
  .container {
    width: 100%;
    margin: 0;
  }
}